// src/components/ReportExport.tsx
// 

import { useState } from 'react';
import { AnalysisResult } from '../types';
import { exportReport } from '../utils/reportGenerator';

interface ReportExportProps {
  result: AnalysisResult;
  onClose: () => void;
}

export default function ReportExport({ result, onClose }: ReportExportProps) {
  //  + 
  const defaultFilename = `${result.basic.packageName.replace(/\./g, '_')}_${new Date().toISOString().slice(0, 10).replace(/-/g, '')}`;

  const [filename, setFilename] = useState(defaultFilename);
  const [format, setFormat] = useState<'html' | 'json'>('html');
  const [includeTimestamp, setIncludeTimestamp] = useState(true);
  const [prettyPrint, setPrettyPrint] = useState(true);
  const [error, setError] = useState<string>('');

  // 
  const sanitizeFilename = (name: string): string => {
    // : / \ : * ? " < > |
    return name.replace(/[\/\\:*?"<>|]/g, '_').trim();
  };

  // 
  const handleFilenameChange = (value: string) => {
    setFilename(value);
    setError('');
  };

  // 
  const handleExport = () => {
    const sanitizedFilename = sanitizeFilename(filename || defaultFilename);

    if (!sanitizedFilename) {
      setError('');
      return;
    }

    try {
      exportReport(result, {
        format,
        filename: sanitizedFilename,
        includeTimestamp,
        prettyPrint: format === 'json' ? prettyPrint : true,
      });
      onClose();
    } catch (error) {
      console.error(':', error);
      setError('');
    }
  };

  // 
  const handleOverlayClick = (e: React.MouseEvent<HTMLDivElement>) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  return (
    <div className="modal-overlay" onClick={handleOverlayClick}>
      <div className="modal-content" onClick={(e) => e.stopPropagation()}>
        {/*  */}
        <div className="modal-header">
          <h2> </h2>
          <button className="modal-close" onClick={onClose}>
            
          </button>
        </div>

        {/*  */}
        <div className="modal-body">
          {/*  */}
          <div className="form-group">
            <label htmlFor="filename"></label>
            <input
              id="filename"
              type="text"
              className={`form-input ${error ? 'input-error' : ''}`}
              value={filename}
              onChange={(e) => handleFilenameChange(e.target.value)}
              placeholder=""
            />
            {error && <p className="error-message">{error}</p>}
            <p className="hint-text">: / \ : * ? " {'<'} {'>'} |</p>
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <div className="radio-group">
              <label className="radio-label">
                <input
                  type="radio"
                  name="format"
                  value="html"
                  checked={format === 'html'}
                  onChange={(e) => setFormat(e.target.value as 'html')}
                />
                <span>HTML ( + )</span>
              </label>
              <label className="radio-label">
                <input
                  type="radio"
                  name="format"
                  value="json"
                  checked={format === 'json'}
                  onChange={(e) => setFormat(e.target.value as 'json')}
                />
                <span>JSON ()</span>
              </label>
            </div>
          </div>

          {/*  */}
          <div className="form-group">
            <label></label>
            <div className="checkbox-group">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={includeTimestamp}
                  onChange={(e) => setIncludeTimestamp(e.target.checked)}
                />
                <span></span>
              </label>
              {format === 'json' && (
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={prettyPrint}
                    onChange={(e) => setPrettyPrint(e.target.checked)}
                  />
                  <span></span>
                </label>
              )}
            </div>
          </div>
        </div>

        {/*  */}
        <div className="modal-footer">
          <button className="button button-secondary" onClick={onClose}>
            
          </button>
          <button className="button" onClick={handleExport}>
            
          </button>
        </div>
      </div>
    </div>
  );
}
