// src/services/apkAnalyzer.ts
// APK  - 

import JSZip from 'jszip';
import { convertAxmlToXml, extractManifestInfo, extractComponents } from '../utils/axmlParser';
import { scanApk, scanMultipleApks, ScanResult } from './sdkScanner';
import { loadRules } from './rulesLoader';
import { fuzzyMatchLibraryWithCache } from '../utils/fuzzyMatcher';
import { parseXapk, isXapkFile } from './xapkParser';
import { AnalysisResult, Library, RulesBundle, AnalysisProgress } from '../types';

/**
 *  APK  XAPK 
 * @param file - APK  XAPK 
 * @param onProgress - 
 * @returns 
 */
export async function analyzeApk(
  file: File,
  onProgress?: (progress: AnalysisProgress) => void
): Promise<AnalysisResult> {
  const fileType = isXapkFile(file) ? 'XAPK' : 'APK';
  console.log(`  ${fileType}: ${file.name} (${(file.size / 1024 / 1024).toFixed(2)} MB)`);

  try {
    let targetApkFile: File = file;
    let xapkInfo: any = null;
    let scanResult: ScanResult;
    
    //  XAPK  APK
    if (isXapkFile(file)) {
      onProgress?.({
        stage: 'extracting',
        message: ' XAPK ...',
        progress: 5,
      });

      xapkInfo = await parseXapk(file);
      targetApkFile = xapkInfo.mainApk;
      console.log(` XAPK  APK: ${xapkInfo.mainApk.name}`);
      console.log(`  APK: ${xapkInfo.configApks.map((f: File) => f.name).join(', ')}`);
    }

    //  1:  APK 
    onProgress?.({
      stage: 'extracting',
      message: ' APK ...',
      progress: 10,
    });

    const zip = await JSZip.loadAsync(targetApkFile);
    console.log('  APK ');

    //  2:  AndroidManifest.xml
    onProgress?.({
      stage: 'parsing',
      message: ' AndroidManifest.xml...',
      progress: 30,
    });

    const manifestFile = zip.file('AndroidManifest.xml');
    if (!manifestFile) {
      throw new Error(' AndroidManifest.xml ');
    }

    const manifestBuffer = await manifestFile.async('arraybuffer');
    const xmlText = convertAxmlToXml(manifestBuffer);
    const basicInfo = extractManifestInfo(xmlText);
    const components = extractComponents(xmlText);

    const parsedManifest = {
      xmlText,
      ...basicInfo,
      ...components,
    };

    console.log(' AndroidManifest.xml ');
    console.log(`  - : ${basicInfo.packageName}`);
    console.log(`  - : ${basicInfo.versionName} (${basicInfo.versionCode})`);

    //  3:  SDK 
    onProgress?.({
      stage: 'scanning',
      message: xapkInfo ? ' APK  SDK ...' : ' SDK ...',
      progress: 50,
    });

    if (xapkInfo) {
      // XAPK:  APK 
      const apkFiles = xapkInfo.allApks.map((apkFile: File) => ({
        file: apkFile,
        isMain: apkFile === xapkInfo.mainApk
      }));
      
      scanResult = await scanMultipleApks(apkFiles, parsedManifest);
      console.log(' XAPK  SDK ');
    } else {
      //  APK: 
      scanResult = await scanApk(zip, parsedManifest);
      console.log(' APK SDK ');
    }

    //  4: 
    onProgress?.({
      stage: 'matching',
      message: '...',
      progress: 60,
    });

    const rules = await loadRules();
    if (!rules) {
      throw new Error('');
    }

    console.log(' ');
    console.log(`  - : ${rules.version}`);
    console.log(`  - : ${rules.totalRules}`);

    //  5: 
    onProgress?.({
      stage: 'matching',
      message: ' SDK ...',
      progress: 80,
    });

    const libraries = await matchLibraries(scanResult, rules);
    console.log(' ');
    console.log(`  -  ${libraries.length} `);

    //  6: 
    onProgress?.({
      stage: 'completed',
      message: '',
      progress: 100,
    });

    const stats = calculateStats(libraries);

    const result: AnalysisResult = {
      basic: basicInfo,
      libraries,
      stats,
      manifestXml: xmlText,
      timestamp: new Date().toISOString(),
    };

    console.log(` ${fileType} `);
    return result;
  } catch (error) {
    console.error(` ${fileType} :`, error);
    onProgress?.({
      stage: 'error',
      message: `: ${error instanceof Error ? error.message : ''}`,
      progress: 0,
    });
    throw error;
  }
}

/**
 * 
 */
async function matchLibraries(
  scanResult: ScanResult,
  rules: RulesBundle
): Promise<Library[]> {
  const libraries: Library[] = [];
  const libraryMap = new Map<string, Library>(); // 

  // 1.  Native 
  console.log('  Native ...');
  for (const libName of scanResult.nativeLibs) {
    const matched = fuzzyMatchLibraryWithCache(libName, rules.rules.native);

    //  countlocationsarchitectures
    const libInfo = scanResult.nativeLibsMap.get(libName);

    if (matched) {
      //  UUID 
      const libraryKey = matched.uuid || matched.id;

      // 
      if (libraryMap.has(libraryKey)) {
        const existing = libraryMap.get(libraryKey)!;

        // 
        if (libInfo) {
          existing.count = (existing.count || 0) + libInfo.count;

          // 
          existing.locations = existing.locations || [];
          existing.locations.push(...libInfo.locations);

          // 
          existing.architectures = existing.architectures || [];
          libInfo.architectures.forEach(arch => {
            if (!existing.architectures!.includes(arch)) {
              existing.architectures!.push(arch);
            }
          });
        }
      } else {
        //  libInfo 
        const library: Library = {
          ...matched,
          count: libInfo?.count || 0,
          locations: libInfo?.locations || [],
          architectures: libInfo?.architectures || [],
          hasMetadata: true,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    } else {
      // 
      const libraryKey = libName;

      if (!libraryMap.has(libraryKey)) {
        const library: Library = {
          id: libraryKey,
          uuid: '',
          name: libName,
          label: libName,
          category: 'other',
          categoryLabel: '',
          categoryIcon: '',
          developer: 'Unknown',
          description: '',
          sourceLink: '',
          type: 'native',
          count: libInfo?.count || 0,
          locations: libInfo?.locations || [],
          architectures: libInfo?.architectures || [],
          hasMetadata: false,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    }
  }

  console.log(` Native : ${libraryMap.size} `);

  // 2.  Activity 
  console.log('  Activity ...');
  for (const activityName of scanResult.activities) {
    const matched = fuzzyMatchLibraryWithCache(activityName, rules.rules.activities);

    if (matched) {
      //  UUID  ID
      //  SDK  UUID
      const libraryKey = matched.uuid || matched.id;

      if (libraryMap.has(libraryKey)) {
        // 
        const existing = libraryMap.get(libraryKey)!;
        existing.count = (existing.count || 0) + 1;
        //  locations
        if (!existing.locations) existing.locations = [];
        existing.locations.push(`Activity: ${activityName}`);
      } else {
        //  SDK
        const library: Library = {
          ...matched,
          count: 1,
          locations: [`Activity: ${activityName}`],
          hasMetadata: true,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    }
  }

  // 3.  Service 
  console.log('  Service ...');
  for (const serviceName of scanResult.services) {
    const matched = fuzzyMatchLibraryWithCache(serviceName, rules.rules.services);

    if (matched) {
      const libraryKey = matched.uuid || matched.id;

      if (libraryMap.has(libraryKey)) {
        const existing = libraryMap.get(libraryKey)!;
        existing.count = (existing.count || 0) + 1;
        if (!existing.locations) existing.locations = [];
        existing.locations.push(`Service: ${serviceName}`);
      } else {
        const library: Library = {
          ...matched,
          count: 1,
          locations: [`Service: ${serviceName}`],
          hasMetadata: true,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    }
  }

  // 4.  Provider 
  console.log('  Provider ...');
  for (const providerName of scanResult.providers) {
    const matched = fuzzyMatchLibraryWithCache(providerName, rules.rules.providers);

    if (matched) {
      const libraryKey = matched.uuid || matched.id;

      if (libraryMap.has(libraryKey)) {
        const existing = libraryMap.get(libraryKey)!;
        existing.count = (existing.count || 0) + 1;
        if (!existing.locations) existing.locations = [];
        existing.locations.push(`Provider: ${providerName}`);
      } else {
        const library: Library = {
          ...matched,
          count: 1,
          locations: [`Provider: ${providerName}`],
          hasMetadata: true,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    }
  }

  // 5.  Receiver 
  console.log('  Receiver ...');
  for (const receiverName of scanResult.receivers) {
    const matched = fuzzyMatchLibraryWithCache(receiverName, rules.rules.receivers);

    if (matched) {
      const libraryKey = matched.uuid || matched.id;

      if (libraryMap.has(libraryKey)) {
        const existing = libraryMap.get(libraryKey)!;
        existing.count = (existing.count || 0) + 1;
        if (!existing.locations) existing.locations = [];
        existing.locations.push(`Receiver: ${receiverName}`);
      } else {
        const library: Library = {
          ...matched,
          count: 1,
          locations: [`Receiver: ${receiverName}`],
          hasMetadata: true,
          expanded: false,
        };

        libraryMap.set(libraryKey, library);
      }
    }
  }

  // 
  libraries.push(...Array.from(libraryMap.values()));
  libraries.sort((a, b) => a.label.localeCompare(b.label));

  return libraries;
}

/**
 * 
 */
function calculateStats(libraries: Library[]): AnalysisResult['stats'] {
  const byCategory: Record<string, number> = {};
  const byType: Record<string, number> = {};

  libraries.forEach(lib => {
    // 
    byCategory[lib.category] = (byCategory[lib.category] || 0) + 1;

    // 
    byType[lib.type] = (byType[lib.type] || 0) + 1;
  });

  return {
    total: libraries.length,
    byCategory,
    byType,
  };
}
