// src/components/FileUploader.tsx
//  - 

import { useState, useRef, useEffect } from 'react';
import { AnalysisResult } from '../types';
import { useTextOverflowDetection } from '../hooks/useTextOverflowDetection';

interface RecentAnalysis {
  id: number;
  fileName: string;
  fileSize: string;
  packageName: string;
  analyzeTime: string;
  result: AnalysisResult;
}

interface FileUploaderProps {
  onFileSelect: (file: File) => void;
  disabled?: boolean;
  recentAnalyses?: RecentAnalysis[];
  onQuickReanalyze?: (record: RecentAnalysis) => void;
  onViewHistory?: () => void;
  onDeleteRecord?: (recordId: number) => void;
  fileValidationError?: string | null;
  onValidationError?: (error: string) => void;
  deletingRecordId?: number | null;
  onSetDeletingRecordId?: (id: number | null) => void;
}

const MAX_FILE_SIZE = 500 * 1024 * 1024; // 500MB
const MIN_FILE_SIZE = 1024; // 1KB

export default function FileUploader({
  onFileSelect,
  disabled = false,
  recentAnalyses = [],
  onQuickReanalyze,
  onViewHistory,
  onDeleteRecord,
  fileValidationError,
  onValidationError,
  deletingRecordId,
  onSetDeletingRecordId,
}: FileUploaderProps) {
  const [isDragOver, setIsDragOver] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);

  // 
  useTextOverflowDetection({
    containerSelector: '.upload-zone',
    textSelector: '.upload-info',
    minPaddingBottom: 20,
    minPaddingTop: 16,
    checkInterval: 500,
    adjustHeight: true,  // 
    debug: false, //  true 
  });

  // 
  useEffect(() => {
    if (deletingRecordId) {
      const scrollContainer = document.querySelector('.upload-analyze-container') as HTMLElement | null;
      if (scrollContainer) {
        const originalOverflow = scrollContainer.style.overflow;
        scrollContainer.style.overflow = 'hidden';
        return () => {
          scrollContainer.style.overflow = originalOverflow;
        };
      }
    }
  }, [deletingRecordId]);

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    if (!disabled) {
      setIsDragOver(true);
    }
  };

  const handleDragLeave = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);
  };

  const handleDrop = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragOver(false);

    if (disabled) return;

    const files = e.dataTransfer.files;
    if (files.length > 0) {
      validateAndProcessFile(files[0]);
    }
  };

  const handleFileInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      validateAndProcessFile(files[0]);
    }
  };

  // 
  const validateAndProcessFile = (file: File) => {
    // 
    onValidationError?.('');

    //  .apk  .xapk
    const fileName = file.name.toLowerCase();
    const isApk = fileName.endsWith('.apk');
    const isXapk = fileName.endsWith('.xapk');
    
    if (!isApk && !isXapk) {
      const error = ' APK  XAPK ';
      onValidationError?.(error);
      return;
    }

    // 
    if (file.size < MIN_FILE_SIZE) {
      const error = ` ${isXapk ? 'XAPK' : 'APK'} `;
      onValidationError?.(error);
      return;
    }

    // 
    if (file.size > MAX_FILE_SIZE) {
      const sizeMB = (file.size / 1024 / 1024).toFixed(2);
      const error = ` (${sizeMB}MB) 500MB`;
      onValidationError?.(error);
      return;
    }

    // 
    onFileSelect(file);
  };

  const handleClick = () => {
    if (!disabled) {
      fileInputRef.current?.click();
    }
  };

  const handleConfirmDelete = (recordId: number) => {
    onDeleteRecord?.(recordId);
  };

  return (
    <div className="file-uploader">
      {/* Hero  -  */}
      <div className="hero-section">
        <h2 className="hero-title"> APK/XAPK  SDK</h2>
        <p className="hero-subtitle">
           APK  XAPK  SDK  2300+ 
        </p>
      </div>

      {/*  -  */}
      <div
        className={`upload-zone ${isDragOver ? 'drag-over' : ''} ${disabled ? 'disabled' : ''}`}
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        onClick={handleClick}
      >
        <div className="upload-content">
          <div className="upload-icon"></div>
          <h3 className="upload-title">
            {disabled ? '...' : ' APK/XAPK '}
          </h3>
          <p className="upload-subtitle">
            <button
              className="link-button"
              onClick={(e) => {
                e.stopPropagation();
                fileInputRef.current?.click();
              }}
            ></button>
          </p>
          <p className="upload-info">: .apk, .xapk | : 500MB</p>
          <input
            ref={fileInputRef}
            type="file"
            accept=".apk,.xapk"
            onChange={handleFileInputChange}
            style={{ display: 'none' }}
            disabled={disabled}
          />
        </div>
      </div>

      {/*  */}
      {fileValidationError && (
        <div className="alert alert-error">
          <span></span>
          <span>{fileValidationError}</span>
        </div>
      )}

      {/*  */}
      {recentAnalyses && recentAnalyses.length > 0 && (
        <div className="recent-analyses">
          <div className="recent-header">
            <h3 className="recent-title"> <span className="analysis-count">({recentAnalyses.length})</span></h3>
            {onViewHistory && (
              <button className="link-button view-all-link" onClick={onViewHistory}>
                 
              </button>
            )}
          </div>
          <ul className="analyses-list">
            {recentAnalyses.map((record) => (
              <li key={record.id} className="analysis-item">
                <div className="item-info">
                  <div className="item-name"> {record.fileName}</div>
                  <div className="item-details">
                    <span className="item-package">{record.packageName}</span>
                    <span className="item-size">{record.fileSize}</span>
                    <span className="item-time">{record.analyzeTime}</span>
                  </div>
                </div>
                <div className="item-actions">
                  <button
                    className="btn btn-sm btn-outline"
                    onClick={() => onQuickReanalyze?.(record)}
                    title=" APK "
                  >
                    
                  </button>
                  <div className="delete-action">
                    <button
                      className="btn btn-icon btn-delete"
                      onClick={() => onSetDeletingRecordId?.(record.id)}
                      title=""
                    >
                      
                    </button>
                    {deletingRecordId === record.id && (
                      <div className="delete-popup-wrapper">
                        <div className="delete-popup-content">
                          <p></p>
                          <div className="confirm-delete-info">
                            <div className="delete-info-item">
                              <span className="info-label"></span>
                              <span className="info-value">{record.fileName}</span>
                            </div>
                            <div className="delete-info-item">
                              <span className="info-label"></span>
                              <span className="info-value">{record.packageName}</span>
                            </div>
                          </div>
                          <div className="confirm-buttons">
                            <button
                              className="btn btn-sm btn-danger"
                              onClick={() => handleConfirmDelete(record.id)}
                            >
                              
                            </button>
                            <button
                              className="btn btn-sm btn-secondary"
                              onClick={() => onSetDeletingRecordId?.(null)}
                            >
                              
                            </button>
                          </div>
                        </div>
                      </div>
                    )}
                  </div>
                </div>
              </li>
            ))}
          </ul>
        </div>
      )}

    </div>
  );
}
